.login-container {
  margin: 100px auto;
  width: 450px;
  height: 200px;
  background: #eee;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 3px 3px 7px #b6b1b1;
}
.link-to {
  margin-top: 10px;
  margin-left: 40px;
  display: block;
  width: 80%;
  height: 35px;
  background: #46a9f0;
  color: white;
  line-height: 35px;
  text-align: center;
  -webkit-transition: all .7s;
  transition: all .7s;
  cursor: pointer;
}
.link-to:hover {
  color: white;
  background: red;
}
.login-container .login-header {
  width: 100%;
  height: 50px;
  background: white;
  text-align: left
}
.login-container .login-header p {
  line-height: 50px;
  font-size: 16px;
  font-weight: bolder;
  text-indent: 1em;
}
.login-container .login-form {
  width: 100%;
  height: 150px;
  background: white;
}
.login-container .login-form input[type=text] {
  margin-left: 12px;
  width: 90%;
}
.login-container .login-form input[type=password] {
  margin-top: 20px;
  margin-left: 12px;
  width: 90%;
}
.login-container .login-form button {
  margin-top: 20px;
  margin-left: 12px;
  width: 90%;
}
.admin {
  position: relative;
}
.admin .ant-layout-header {
  background: #a8d2f0;
}
.admin .admin-title {
  text-align: center;
  color: black;
  font-size: 30px;
  font-weight: bolder;
  line-height: 1.9;
}
.admin .ant-layout-sider-children {
  width: 256px;
  background: white;
}
.admin .ant-menu ant-menu-sub ant-menu-inline {
  width: 100%;
  height: 500px;
}
/* Content组件样式 */
.admin .ant-layout.ant-layout-has-sider > .ant-layout-content {
  padding: 60px;
  height: 800px;
}
/* 可视化数据组件样式 */
.admin .visible-data {
  width: 100%;
  height: 300px;
  background: #faf9f9;
}
.admin .visible-data .visible-view {
  position: relative;
  float: left;
  margin-top: 30px;
  margin-left: 40px;
  padding: 30px;
  width: 300px;
  height: 250px;
  background: white;
  text-align: center;
  line-height: 200px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: .7s;
  transition: .7s;
}
.admin .visible-data .visible-view:hover {
  width: 310px;
  height: 260px;
}
.admin .visible-data .visible-view > span {
  color: #673ab7;
  font-size: 30px;
}
.admin .visible-data .visible-view > h1 {
  position: absolute;
  left: 133px;
  top: 155px;
  height: 50px;
  color: white;
  font-size: 35px;
}
/* 用户数据组件样式 */
.admin .user-data {
  width: 100%;
  height: 300px;
  background: white;
}

/* 商品组件样式 */
.admin .commodity-data {
  width: 100%;
  height: 700px;
  background: white;
  overflow: auto;
}

/* 增加框样式 */
.admin .add_box {
  position: absolute;
  left: 42%;
  top: 23%;
  width: 300px;
  height: 300px;
  background: #eee;
  box-shadow: 3px 2px 10px #8f8f8f;
}

.admin .add_box .add_box_title {
  width: 100%;
  text-align: center;
  background: #ddd;
  box-shadow: 1px 3px 1px #f58cfc;
}
.admin .add_box .add_box_title span {
  font-size: 26px; 
  color: black;
  font-weight: bolder;
}
.admin .add_box .add_box_form {
  padding: 10px;
  width: 100%;
  height: 200px;
  background: #ccc;
}
.admin .add_box .add_box_form input {
  width: 270px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

